import React from "react"

export default function Politykascreen() {
  return (
    <div className='mx-auto max-w-2xl py-4 px-4 sm:py-24 sm:px-6 md:py-16 lg:max-w-7xl lg:px-8'>
      <div className='mx-auto max-w-7xl'>
        <div className='flex flex-col items-center justify-center gap-5'>
          <div>
            <h3 className='text-2xl font-bold'>
              <label>Polityka prywatnoci</label>
            </h3>
          </div>
          <div className='flex flex-col gap-5 [&>*]:rounded-md [&>*]:bg-neutral-200 [&>*]:p-4 [&>*]:dark:bg-neutral-800'>
            <p>
              Polityka prywatnoci okrela zasady ochrony danych osobowych
              Uytkownikw korzystajcych z Serwisu.
            </p>
            <div className='flex flex-col gap-5'>
              <h5 id='dane-osobowe' className='text-xl'>
                Dane osobowe.
              </h5>
              <p>
                Administratorem danych osobowych jest 3klik s.c., ul.
                Konarskiego 25, 11-500 Giycko. (zwana dalej Administratorem
                lub Sprzedawc lub Usugodawc).
              </p>

              <p>
                Dane osobowe s przetwarzane zgodnie z ustaw o ochronie danych
                osobowych oraz ustaw o wiadczeniu usug drog elektroniczn.
              </p>

              <p>
                Podanie danych osobowych jest dobrowolne, ale niezbdne w celu
                korzystania z penej funkcjonalnoci Serwisu, w tym do zawarcia
                Umowy Sprzeday.
              </p>

              <p>
                Administrator dokada szczeglnej starannoci w celu ochrony
                interesw osb, ktrych dane dotycz, a w szczeglnoci
                zapewnia, e zbierane przez niego dane s przetwarzane zgodnie z
                prawem; zbierane dla oznaczonych, zgodnych z prawem celw i
                niepoddawane dalszemu przetwarzaniu niezgodne z tymi celami;
                merytorycznie poprawne i adekwatne w stosunku do celw, w jakich
                s przetwarzane oraz przechowywane w postaci umoliwiajcej
                identyfikacj osb, ktrych dotycz, nie duej ni jest to
                niezbdne do osignicia celu przetwarzania.
              </p>

              <p>
                Klient wyraa zgod na przetwarzanie podanych danych osobowych
                przez Administratora w celu prowadzenia konta, zapamitania
                historii zakupw Klienta oraz jego preferencji.
              </p>

              <p>
                W przypadku Klienta, ktry korzysta w Serwisie ze sposobu
                patnoci elektronicznych Administrator udostpnia zebrane dane
                osobowe Klienta, wybranemu podmiotowi obsugujcemu powysze
                patnoci w Serwisie.
              </p>
            </div>
            <div className='flex flex-col gap-5'>
              <h5 id='cookies'>Serwis korzysta z plikw cookies.</h5>

              <p>
                Pliki Cookies (ciasteczka) s to niewielkie informacje tekstowe
                w postaci plikw tekstowych, wysyane przez serwer i
                przechowywane zapisywane po stronie osoby odwiedzajcej stron
                Serwisu (np. na dysku twardym komputera, laptopa, czy te na
                karcie pamici smartfona  w zalenoci z jakiego urzdzenia
                korzysta odwiedzajcy). Informacje zawarte w danym pliku cookie
                mog by odczytane wycznie przez stron, z ktrej ten plik
                pochodzi.
              </p>

              <p>
                Przegldarki internetowe zazwyczaj domylnie dopuszczaj
                przechowywanie plikw cookies w urzdzeniu kocowym Uytkownika.
                Kady ma moliwo okrelenia warunkw korzystania z plikw
                Cookies za pomoc ustawie wasnej przegldarki internetowej.
                Oznacza to, e mona np. czciowo ograniczy (np. czasowo) lub
                cakowicie wyczy moliwo zapisywania plikw Cookies.
                Ograniczenia takie mog jednak zakci moliwo korzystania z
                funkcjonalnoci Serwisu.
              </p>
              <p>Pliki cookies wykorzystywane s w nastpujcych celach:</p>
              <ul>
                <li>
                  utrzymanie sesji Uytkownika Serwisu (bez koniecznoci
                  logowania), dziki ktrej Uytkownik korzystajcy z tego
                  samego urzdzenia nie musi za kadym razem logowa si w
                  serwisie Serwisu ponownie wpisywa loginu i hasa
                </li>
                <li>
                  utrzymanie sesji Uytkownika Serwisu (po zalogowaniu), dziki
                  ktrej Uytkownik nie musi na kadej podstronie Serwisu
                  ponownie wpisywa loginu i hasa
                </li>
                <li>
                  prowadzenia anonimowych statystyk przedstawiajcych sposb
                  korzystania z Serwisu;
                </li>
                <li>
                  zapamitywania danych z wypenionych Formularzy Zamwienia.
                </li>
                <li>
                  wykonania patnoci poprzez formularz w 2 etapie zamwienia w
                  koszyku.
                </li>
              </ul>
            </div>
            <div className='flex flex-col gap-5'>
              <h5>
                <label>INFORMACJE DOTYCZCE PRZETWARZANIA DANYCH</label>
              </h5>
              <p>
                1. Administratorem danych jest 3klik s.c., ul. Konarskiego 25,
                11-500 Giycko.
              </p>
              <p>
                2. U administratora danych osobowych nie zosta wyznaczony
                inspektor ochrony danych.
              </p>
              <p>3. Pani/Pana dane bd przetwarzane w celu:</p>
              <ul>
                <li>
                  realizacji zamwienia oraz zobowiza administratora
                  wynikajcych z umowy sprzeday  podstaw prawn przetwarzania
                  jest niezbdno przetwarzania do wykonania umowy sprzeday
                  (art. 6 ust. 1 lit. b RODO)
                </li>
                <li>
                  marketingu bezporedniego produktw i usug administratora, w
                  tym w celach analitycznych i profilowania  podstaw prawn
                  przetwarzania danych jest niezbdno przetwarzania do
                  realizacji prawnie uzasadnionego interesu administratora (art.
                  6 ust. 1 lit. f RODO); uzasadnionym interesem administratora
                  jest moliwo przedstawienia Pani/Panu informacji o usugach
                  administratora, przedstawienia przez niego ofert dopasowanych
                  do Pani/Pana potrzeb i zainteresowa oraz zwikszenie
                  sprzeday usug administratora; dodatkow podstaw dla
                  marketingu bezporedniego produktw i usug administratora
                  jest Pani/Pana zgoda (art. 6 ust. 1 lit. a RODO)
                </li>
                <li>
                  poprawnego dziaania strony www.3klik.pl, a w szczeglnoci
                  zapobiegania oraz wykrywania naduy, a take optymalizacji
                  korzystania ze strony internetowej w zakresie w jakim pliki
                  cookie mog zbiera dane o Pani/Pana zachowaniu w sieci czy
                  adresie IP, dane pochodzce z cookie moemy poczy z danymi
                  pochodzcymi z zamwie, zbieraniu zaagregowanych statystyk,
                  utrzymania sesji uytkownika serwisu internetowych zakupw (po
                  zalogowaniu), dziki ktrej uytkownik nie musi na kadej
                  podstronie serwisu ponownie wpisywa loginu i hasa  podstaw
                  prawn przetwarzania danych jest Pani/Pana zgoda (art. 6 ust.
                  1 lit. a RODO)
                </li>
                <li>
                  ustalenia, dochodzenia lub obrony przed roszczeniami 
                  podstaw prawn przetwarzania danych jest niezbdno
                  przetwarzania do realizacji prawnie uzasadnionego interesu
                  administratora (art. 6 ust. 1 lit. f RODO); uzasadnionym
                  interesem administratora jest ochrona przed roszczeniami
                  zwizanymi z czonkostwem w Programie.
                </li>
              </ul>
              <p>
                4. Przysuguje Pani/Panu prawo dostpu do Pani/Pana danych oraz
                prawo dania ich sprostowania, ich usunicia lub ograniczenia
                ich przetwarzania.
              </p>
              <p>5. Administrator bdzie przetwarza dane:</p>
              <ul>
                <li>
                  przez okres posiadania przez Pana/Pani konta na stronie
                  www.3klik.pl
                </li>
                <li>
                  do czasu zgoszenia przez Pana/Pani sprzeciwu w zakresie, w
                  jakim Pani/Pana dane osobowe s przetwarzane w celu marketingu
                  bezporedniego produktw i usug administratora, w tym w
                  celach analitycznych i profilowania
                </li>
                <li>
                  przez okres nie duszy ni okresy przedawnie w sprawach
                  cywilnych i karnych ustalone zgodnie z obowizujcymi
                  przepisami polskiego prawa w zakresie, w jakim Pani/Pana dane
                  osobowe bd przetwarzane w celu ustalenia, dochodzenia lub
                  obrony przed roszczeniami
                </li>
                <li>
                  w zakresie plikw cookies sesyjnych przez okres do 2 lat lub
                  do czasu ich usunicia z przegldarki internetowej, z kolei w
                  zakresie plikw staych cookies pozostaj w przegldarce do
                  momentu terminu ich wyganicia lub do momentu ich usunicia
                  przez uytkownika z przegldarki
                </li>
              </ul>
              <p>
                6. W zakresie, w jakim podstaw przetwarzania Pani/Pana danych
                osobowych jest przesanka prawnie uzasadnionego interesu
                administratora, przysuguje Pani/Panu prawo wniesienia sprzeciwu
                wobec przetwarzania Pani/Pana danych osobowych.
              </p>
              <p>
                7. W szczeglnoci przysuguje Pani/Panu prawo sprzeciwu wobec
                przetwarzania danych na potrzeby marketingu bezporedniego oraz
                profilowania.
              </p>
              <p>
                8. W zakresie, w jakim podstaw przetwarzania Pani/Pana danych
                osobowych jest zgoda, ma Pani/Pan prawo wycofania zgody.
                Wycofanie zgody nie ma wpywu na zgodno z prawem
                przetwarzania, ktrego dokonano na podstawie zgody przed jej
                wycofaniem.
              </p>
              <p>
                9. W zakresie, w jakim Pani/Pana dane s przetwarzane w celu
                zawarcia i wykonywania umowy lub przetwarzane na podstawie zgody
                 przysuguje Pani/Panu take prawo do przenoszenia danych
                osobowych, tj. do otrzymania od administratora Pani/Pana danych
                osobowych, w ustrukturyzowanym, powszechnie uywanym formacie
                nadajcym si do odczytu maszynowego. Moe Pani/Pan przesa te
                dane innemu administratorowi danych.
              </p>
              <p>
                10. Przysuguje Pani/Panu rwnie prawo wniesienia skargi do
                organu nadzorczego zajmujcego si ochron danych osobowych.
              </p>
              <p>
                11. W celu skorzystania z powyszych praw naley skontaktowa
                si z administratorem danych. Dane kontaktowe wskazane s wyej.
              </p>
              <p>12. Dostp do Pani/Pana danych osobowych bd mie:</p>
              <ul>
                <li>
                  upowanieni pracownicy administratora i tylko w niezbdnym
                  zakresie
                </li>
                <li>
                  podmioty przetwarzajcym dane na zlecenie administratora, w
                  szczeglnoci dostawcy usug IT, agencje marketingowe, firmy
                  obsugujce call center oraz dostawy  przy czym takie
                  podmioty przetwarzaj dane na podstawie umowy z
                  administratorem i wycznie zgodnie z poleceniami
                  administratora
                </li>
                <li>
                  osoby trzecie, w szczeglnoci uprawnione organy jeeli
                  obowizek taki wynika z bezwzgldnie obowizujcych przepisw
                  prawa.
                </li>
              </ul>
              <p>
                13. Pani/Pana dane osobowe nie bd przekazywane do odbiorcw
                znajdujcych si w pastwach poza Europejskim Obszarem
                Gospodarczym.
              </p>
              <p>
                14. Podanie danych osobowych jest dobrowolne aczkolwiek
                niezbdne do realizacji umowy sprzeday i umoliwienia dostpu
                do serwisu na stronie www.3klik.pl.
              </p>
              <p>
                15. Administrator przetwarza rwnie anonimowo dane
                eksploatacyjne zwizane z korzystaniem z Serwisu (adres IP,
                host, domena) do generowania statystyk pomocnych w
                administrowaniu Sklepem Internetowym.
              </p>
              <p>
                16. Uytkownikowi przysuguje prawo wgldu w jego gromadzone
                dane osobowe, prawo do ich poprawy, do dania usunicia danych
                oraz Konta. W celu realizacji tych uprawnie Uytkownik powinien
                skontaktowa si z administratorem pisemnie lub drog
                elektroniczn na adres wskazany w Regulaminie. W przypadku
                zgoszenia dania usunicia danych jest to rwnoznaczne z
                usuniciem Konta.
              </p>
            </div>
          </div>
          {/* KONIEC DROPDOWN */}
        </div>
      </div>
    </div>
  )
}
