import React from "react"
import { usePageTitle } from "../../hooks/usePageTitle"

export default function Onasscreen() {
  usePageTitle("O nas")
  return (
    <div className='mx-auto max-w-2xl py-4 px-4 sm:py-24 sm:px-6 md:py-16 lg:max-w-7xl lg:px-8'>
      <div className='mx-auto max-w-7xl'>
        <div className='flex w-full flex-col items-center justify-center gap-5 lg:max-w-7xl lg:px-8'>
          <h1 className='text-2xl font-bold'>O nas</h1>
          <div className='flex flex-col gap-5 sm:flex-row'>
            <div className='neutral-200 flex w-full items-center rounded-md p-5 text-center dark:bg-neutral-800 sm:w-1/2'>
              <p>
                Zawsze usiowalimy odywia si zdrowo i kolorowo. Dlaczego
                "usiowalimy"?, bo z braku czasu, czciej ni tego chcielimy,
                z wyrzutami sumienia kupowalimy cokolwiek w pobliskim
                supermarkecie
              </p>
            </div>
            <div className='neutral-200 flex w-full items-center rounded-md p-5 text-center dark:bg-neutral-800 sm:w-1/2'>
              <p>
                "Usiowalimy" rwnie dlatego, e prby "upolowania" zdrowych
                lokalnych produktw, wyjazdu za miasto do rolnika i wyprawy na
                bazar zajmoway nam tak wiele czasu, e po kolejnej "porace"
                gdy niestety nie zdylimy si ju zapa bo "pani Krysia
                wszystko wyprzedaa, postanowilimy co z tym zrobi.
              </p>
            </div>
          </div>
          <div className='flex w-full flex-col items-center gap-5 rounded-md bg-neutral-200 p-5 dark:bg-neutral-800'>
            {/* <p className='text-xl font-bold'>
              Nasza firma zrodzia si z uczu gboko ludzkich - z mioci i
              nienawici, ze wstrtu i sentymentu:
            </p> */}
            <p className='text-xl font-bold'>
              Nasza firma zrodzia si z uczu gboko ludzkich:
            </p>
            <ul className='flex flex-col gap-5 sm:gap-2'>
              <li>
                <b>Z frustracji</b> z powodu ogromu czasu powiconego na prby
                zakupu wieej, zdrowej ywnoci
              </li>
              <li>
                <b>Z pasji</b> do tworzenia zbilansowanych, szybkich i pysznych
                posikw
              </li>
              <li>
                <b>Ze wiadomoci</b>, e zdrowie jest najwaniejsze bo "jeste
                tym co jesz"
              </li>
              <li>
                <b>Z wstrtu</b> do sztucznych, pozbawionych smaku, penych
                chemii produktw z zagranicznych supermarketw
              </li>
              <li>
                <b>Z sentymentu</b> do naturalnych, penych aromatu smakw z
                dziecistwa
              </li>
              <li>
                <b>Z niechci</b> sponsorowania producentw produkujcych
                opryskiwan, pen hormonw, skaon ywno
              </li>
              <li>
                <b>Z chci</b> wspierania polskich jak i zagranicznych rolnikw
                produkujcych zdrow ywno
              </li>
              <li>
                <b>Z mioci i szacunku</b> do naszej planety i wiadomoci
                tego, jak wszystko jest ze sob poczone
              </li>
            </ul>
          </div>
          <div className='p-5 text-center text-2xl font-bold'>
            <p>
              Wszystko to poczylimy w jak najlepszy sposb, dodalimy troch
              tego, tamtego i powsta 3KLIK. Ostatnia ksika kucharska jakiej
              potrzebujesz.
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
